import React, { useEffect, useState } from "react";
import { Nav, Table } from "react-bootstrap";
import { useParams, useHistory } from "react-router-dom";
import "../css/Detail.css";
import { DetailStore } from "../Zustore";
export function Detail({ shoes, stock }) {
  const [tab, setTab] = useState(0);
  const [alert, setAlert] = useState(true);
  const [mySwitch, setMySwitch] = useState(false);
  const history = useHistory();

  useEffect(() => {
    let timer = setTimeout(() => {
      setAlert(false);
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, []);
  let { id } = useParams();
  let IntId = parseInt(id);
  const [detailStock, setDetailStock] = useState(stock[id]);
  function StockInfo() {
    return (
      <div>
        {detailStock > 0 ? (
          <p> : {detailStock}</p>
        ) : (
          <p> .</p>
        )}
      </div>
    );
  }

  function Go2home() {
    history.push("/");
  }

  return (
    <div className="container">
      <div className="row detail-main">
        <DetailHead alert={alert} />
        <DetailBody
          IntId={IntId}
          shoes={shoes}
          id={id}
          setDetailStock={setDetailStock}
          detailStock={detailStock}
          Go2home={Go2home}
          StockInfo={StockInfo}
        />
        <TabBtn setTab={setTab} />

        <TabModal
          tab={tab}
          shoes={shoes}
          id={id}
          mySwitch={mySwitch}
          setMySwitch={setMySwitch}
        />
      </div>
    </div>
  );
}

function DetailHead({ alert }) {
  return (
    <>
      <h2 className="detail-title">Detail</h2>
      {alert ? (
        <div className="detail-alert">
          <div className="detail-alert__inner">
               .
          </div>
        </div>
      ) : null}
    </>
  );
}

function DetailBody({
  IntId,
  shoes,
  id,
  setDetailStock,
  detailStock,
  Go2home,
  StockInfo,
}) {
  const { ownCart } = DetailStore();
  const [toCart, setToCart] = useState(false);
  function myCart(e) {
    toCart
      ? alert("  .")
      : ownCart.push({ id: id, name: shoes[id].title, quan: 1 });
    setToCart(true);
  }
  return (
    <>
      <div className="col-md-6">
        <img
          src={`https://codingapple1.github.io/shop/shoes${IntId + 1}.jpg`}
          alt="img"
          width="100%"
        />
      </div>
      <div className="col-md-6 mt-4">
        <h4 className="pt-5">{shoes[id].title}</h4>
        <p>{shoes[id].content}</p>
        <p>{shoes[id].price}</p>
        <StockInfo />
        <button
          className="btn btn-danger"
          style={{ marginRight: "5px" }}
          onClick={() => {
            myCart();

            toCart ? console.log() : alert("  .");

            toCart ? console.log() : setDetailStock(detailStock - 1);
          }}
        >
          
        </button>
        <button
          className="btn btn-danger"
          style={{ marginLeft: "5px" }}
          onClick={Go2home}
        >
          
        </button>
      </div>
    </>
  );
}

function TabBtn({ setTab }) {
  return (
    <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
      <Nav.Item>
        <Nav.Link
          eventKey="link-0"
          onClick={() => {
            setTab(0);
          }}
        >
          
        </Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link
          eventKey="link-1"
          onClick={() => {
            setTab(1);
          }}
        >
          
        </Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link
          eventKey="link-2"
          onClick={() => {
            setTab(2);
          }}
        >
          
        </Nav.Link>
      </Nav.Item>
    </Nav>
  );
}

function TabModal({ tab, id, shoes, setMySwitch }) {
  if (tab === 0) {
    return (
      <Table striped bordered hover>
        <thead>
          <tr>
            <th className="detail-table">#</th>
            <th colSpan={3}> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}>{shoes[id].title}</td>
          </tr>

          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}>{shoes[id].price}</td>
          </tr>
          <tr>
            <td className="detail-table"> </td>
            <td colSpan={4}> </td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}> </td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}></td>
          </tr>
          <tr>
            <td className="detail-table"> </td>
            <td colSpan={4}> Tag </td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}> Tag </td>
          </tr>
        </tbody>
      </Table>
    );
  } else if (tab === 1) {
    return (
      <Table striped bordered hover>
        <thead>
          <tr>
            <th className="detail-table">#</th>
            <th colSpan={3}></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td className="detail-table"> / </td>
            <td colSpan={4}>  </td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}>  </td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}>19980805</td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}>xxxxxxx@gmail.com</td>
          </tr>
          <tr>
            <td className="detail-table"></td>
            <td colSpan={4}> </td>
          </tr>
        </tbody>
      </Table>
    );
  } else {
    return (
      <div>
        <div>
          <h5 className="mt-3">  /     </h5>
        </div>
        <div className="detail-modal">
          <div className="detail-modal__title"></div>
          <div className="detail-modal__text">
            <ul>
              <li>    50% .</li>
              <li>
                         .
              </li>
              <li>  1~3  .</li>
            </ul>
          </div>
        </div>
        <div className="detail-modal">
          <div className="detail-modal__title"></div>
          <div className="detail-modal__text">
            <ul>
              <li>(99999)      123-4</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
